.que_box {
  border: 1px solid #e3e4eb;
  padding: 37px 58px;
  position: relative;
}
.que_box h2 {
  font-family: Poppins;
  font-style: normal;
  font-weight: normal;
  font-size: 26px;
  line-height: 39px;
  text-align: center;
  color: #000000;
  position: absolute;
  top: -20px;
  background-color: #fff;
  padding: 0 15px;
}
.que_box p {
  font-family: Poppins;
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 22px;
  color: #000000;
}
.que_box ul {
  padding: 0;
}
.que_box ul li {
  font-family: Poppins;
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 180%;
  color: #000000;
  list-style: none;
  padding-bottom: 11px;
}
.que_num {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.que_status h2 {
  font-family: Poppins;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 30px;
  text-align: center;
  color: #000000;
  background: #e3e4eb;
  border: 1px solid #e3e4eb;
  padding: 10px 25px;
  margin-bottom: 18px;
}
p#col_gre {
  background: #7fdf05;
  border: 1px solid #e3e4eb;
  width: 26px;
  height: 26px;
  border-radius: 50px;
}
p#col_grey {
  color: #4f4f4f;
  background: #e3e4eb;
  border: 1px solid #e3e4eb;
  width: 26px;
  height: 26px;
  border-radius: 50px;
}
.que_num p {
  font-family: Poppins;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 25px;
  text-align: center;
  color: #4f4f4f;
}
.que_status {
  border: 1px solid #e3e4eb;
}
.que_status .que_num {
  padding: 0px 15px;
}
.time_set p img {
  margin-right: 10.31px;
  max-width: 100%;
}
.time_set p {
  text-align: right;
  font-family: Poppins;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #000000;
}
.ans_not ul li {
  font-family: Poppins;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 21px;
  color: #000000;
  list-style: none;
  position: relative;
}
.ans_not ul li span.dotte1 {
  width: 14px;
  height: 14px;
  background-color: #7fdf05;
  border-radius: 50%;
  position: absolute;
  left: -20px;
  top: 3px;
}
.ans_not ul li span.dotte2 {
  width: 14px;
  height: 14px;
  background-color: #e3e4eb;
  border-radius: 50%;
  position: absolute;
  left: -20px;
  top: 3px;
}
.ans_not ul {
  display: flex;
  justify-content: space-evenly;
  float: right;
  gap: 3rem;
}
.ans_not {
  margin: 20px 0;
}
.circular_progress_module h3 {
  font-family: Poppins;
  font-style: normal;
  font-weight: 500;
  font-size: 30px;
  line-height: 45px;
  color: #323232;
}

span.MuiLinearProgress-root {
  border-radius: 15px;
}
.prev_next_btn {
  margin-bottom: 35px;
}
.circular_progress_module .red1-progress .MuiSlider-track {
  background: #ff3300;
}
.circular_progress_module .red1-progress span.MuiSlider-thumbColorPrimary {
  height: 26px;
  width: 26px;
  background: #ff3300 !important;
  box-shadow: 0px 0px 10px rgb(236 124 211) !important;
}
.circular_progress_module .yellow1-progress .MuiSlider-track {
  background: #ff9900;
}
.circular_progress_module .yellow1-progress span.MuiSlider-thumbColorPrimary {
  height: 26px;
  width: 26px;
  background: #ff9900 !important;
  box-shadow: 0px 0px 10px rgb(236 124 211) !important;
}

.circular_progress_module .green1-progress .MuiSlider-track {
  background: #72b51d;
}
.circular_progress_module .green1-progress span.MuiSlider-thumbColorPrimary {
  height: 26px;
  width: 26px;
  background: #72b51d !important;
  box-shadow: 0px 0px 10px rgb(236 124 211) !important;
}

@media screen and (min-device-width: 50px) and (max-device-width: 767px) {
  .que_box {
    padding: 30px 16px;
  }
  .maindiv_prog {
    margin: 45px 0;
  }
}
